  $warning-color: #eaab00;

//CONFIG COLORS
  $type-status: "default" "gray" "lightgray" "first" "second" "third" "danger" "info" "success" "warning";
  $color-status: $default-color $gray-color $lightgray-color $first-color $second-color $third-color $danger-color $info-color $success-color $warning-color;
